import { FC, useEffect, useState } from "react";
import {
  BeforeInstallPromptEventHolder,
  BeforeInstallPromptEventHolderProvider,
} from "../../contexts/BeforeInstallPromptEventHolderContext";

const BeforeInstallPromptSubscriber: FC = ({ children }) => {
  const [
    beforeInstallPromptEventHolder,
    setBeforeInstallPromptEventHolder,
  ] = useState<null | BeforeInstallPromptEventHolder>(null);

  useEffect(() => {
    const eventName = "beforeinstallprompt";

    const clear = () => setBeforeInstallPromptEventHolder(null);

    const onBeforeInstallPrompt: EventListener = (e: Event) => {
      // Prevent the mini-infobar from appearing on mobile
      e.preventDefault();
      // Stash the event so it can be triggered later.
      setBeforeInstallPromptEventHolder({
        event: e as BeforeInstallPromptEvent,
        clear,
      });
    };

    window.addEventListener(eventName, onBeforeInstallPrompt);

    return () => window.removeEventListener(eventName, onBeforeInstallPrompt);
  }, []);

  return (
    <BeforeInstallPromptEventHolderProvider
      value={beforeInstallPromptEventHolder}
    >
      {children}
    </BeforeInstallPromptEventHolderProvider>
  );
};

export default BeforeInstallPromptSubscriber;
