import { FC, useState } from "react";

const MountUnmount: FC = ({ children }) => {
  const [mounted, setMounted] = useState(true);

  return (
    <>
      <button
        onClick={() => {
          setMounted((old) => !old);
        }}
      >
        {mounted ? "Unmount" : "Mount"}
      </button>

      {mounted && <>{children}</>}
    </>
  );
};

export default MountUnmount;
