import { FC, useContext } from "react";
import { BeforeInstallPromptEventHolderContext } from "../../contexts/BeforeInstallPromptEventHolderContext";

type InstallAdviserProps = {};

const InstallAdviser: FC<InstallAdviserProps> = () => {
  const deferredPrompt = useContext(BeforeInstallPromptEventHolderContext);

  return (
    <div>
      <span>Добавь иконку на экран телефона для быстрого запуска</span>

      {!!deferredPrompt?.event && (
        <button
          onClick={() => {
            deferredPrompt.event.prompt();

            deferredPrompt.event.userChoice.then((choiceResult) => {
              if (choiceResult.outcome === "accepted") {
                // User allowed installation
                deferredPrompt.clear();
              } else {
                // User did not allow installation
                // todo should we clear it in this case???
              }
            });
          }}
        >
          Добавить иконку
        </button>
      )}
    </div>
  );
};

export default InstallAdviser;
